import { Button, Form, InputGroup, Nav, NavDropdown } from 'react-bootstrap';
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import { BOARDS } from './App';

function NavigationBar(props: {
    onBoardChange: (board: string) => void,
    onIntervalPick: (n: number) => void,
    onProbabilityPick: (n: number) => void,
    onTrendPick: (n: number) => void,
    onSearchChange: (s: string) => void,
    board: string,
    interval: number,
    probability: number,
    trend: number,
    current: number,
    maximum: number,
    search: string,
    fractions: Array<number>,
    sections: Array<number>,
}) {
    // https://stackoverflow.com/questions/39823681/read-the-current-full-url-with-react
    // console.log(window.location.pathname)
    // console.log(window.location.href)
    // let board = window.location.pathname.replaceAll('/', '') || 'TQBR';

    return (
        <Navbar expand="lg" className="bg-body-tertiary justify-content-start" fixed="top">
            <Container>
                <Navbar.Brand onClick={() => props.onBoardChange('TQBR')}><b> </b></Navbar.Brand>
                <NavDropdown title={`(${props.board.toUpperCase()}) ${BOARDS[props.board.toUpperCase()]}`} id="basic-nav-dropdown">
                    <NavDropdown.Item onClick={() => props.onBoardChange('STOCKS')}>
                        Nasdaq: Stocks
                    </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => props.onBoardChange('ETFS')}>
                        Nasdaq: ETFs
                    </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item onClick={() => props.onBoardChange('RFUD')}>
                        (RFUD) 
                    </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => props.onBoardChange('ROPD')}>
                        (ROPD) 
                    </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item onClick={() => props.onBoardChange('BOND')}>
                        (BOND) TQCB + TQOB: 
                    </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => props.onBoardChange('TQBR')}>
                        (TQBR) +:    - .
                    </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => props.onBoardChange('TQIF')}>
                        (TQIF) +:  - . ()
                    </NavDropdown.Item>
                    <NavDropdown.Item onClick={() => props.onBoardChange('TQTF')}>
                        (TQTF) +: ETF - . ()
                    </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item onClick={() => props.onBoardChange('CETS')}>
                        (CETS)   - . ()
                    </NavDropdown.Item>
                </NavDropdown>
                <Navbar.Collapse className="justify-content-start">
                    <div className='ps-3'>
                        ({props.current}/{props.maximum})
                    </div>
                    {
                        !props.sections.length && !props.fractions.length &&
                        <div className='ps-3'>
                            <Button className='p-0' variant="secondary" onClick={() => props.onIntervalPick(1)}>
                                <img src={props.interval === 1 ? 'dist-1.png' : 'dist-1-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onIntervalPick(2)}>
                                <img src={props.interval === 2 ? 'dist-2.png' : 'dist-2-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onIntervalPick(3)}>
                                <img src={props.interval === 3 ? 'dist-3.png' : 'dist-3-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onIntervalPick(4)}>
                                <img src={props.interval === 4 ? 'dist-4.png' : 'dist-4-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onIntervalPick(5)}>
                                <img src={props.interval === 5 ? 'dist-5.png' : 'dist-5-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onIntervalPick(6)}>
                                <img src={props.interval === 6 ? 'dist-6.png' : 'dist-6-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                        </div>
                    }
                    {
                        (props.sections.length || props.fractions.length) &&
                        <div className='ps-3'>
                            <Button className='p-0' variant="secondary" onClick={() => props.onProbabilityPick(1)}>
                                <img src={props.probability === 1 ? '20.png' : '20-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onProbabilityPick(2)}>
                                <img src={props.probability === 2 ? '40.png' : '40-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onProbabilityPick(3)}>
                                <img src={props.probability === 3 ? '60.png' : '60-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onProbabilityPick(4)}>
                                <img src={props.probability === 4 ? '80.png' : '80-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                            <Button className='p-0' variant="secondary" onClick={() => props.onProbabilityPick(5)}>
                                <img src={props.probability === 5 ? '100.png' : '100-g.png'} style={{ width: '35px', height: '35px' }} />
                            </Button>
                        </div>
                    }
                    <div className='ps-3'>
                        <Button className='p-0' variant="secondary" onClick={() => props.onTrendPick(1)}>
                            <img src={props.trend === 1 ? 'up.png' : 'up-g.png'} style={{ width: '35px', height: '35px' }} />
                        </Button>
                        <Button className='p-0' variant="secondary" onClick={() => props.onTrendPick(2)}>
                            <img src={props.trend === 2 ? 'down.png' : 'down-g.png'} style={{ width: '35px', height: '35px' }} />
                        </Button>
                    </div>
                </Navbar.Collapse>
                <Navbar.Collapse className="justify-content-end">
                    <InputGroup className="ps-3">
                        <Form.Control
                            id='text-search'
                            type="text"
                            placeholder=""
                            className="mr-sm-2"
                            onChange={(event) => props.onSearchChange(event.target.value)}
                            value={props.search}
                        />
                        <Button
                            variant="outline-secondary"
                            id="button-addon2"
                            onClick={() => {
                                (document.getElementById('text-search') as any).value = '';
                                props.onSearchChange('');
                            }}
                        >
                            X
                        </Button>
                    </InputGroup>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default NavigationBar;