import { Chart, TimeScale } from 'chart.js/auto';
import React, { useEffect, useRef, useState } from 'react';

import 'chartjs-adapter-luxon';
import zoomPlugin from 'chartjs-plugin-zoom';

import Spinner from 'react-bootstrap/Spinner';
import ModalMixed from './ModalMixed';

import * as JSZip from 'jszip';
import { Security } from './Preview';

Chart.register(zoomPlugin, TimeScale);

function Preview3(props: { symbol: string, board: string, description: string, sections: Array<number> }) {
    const [data, setData] = useState<Security | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    // Modal
    const [show, setShow] = useState<boolean>(false);

    useEffect(() => {
        fetch(`output/${props.board.toUpperCase()}-${props.symbol.toUpperCase()}.json.zip`)
            .then((response) => {
                if (!response.ok) {
                    throw new Error(`This is an HTTP error: The status is ${response.status}`);
                }

                // https://developer.mozilla.org/en-US/docs/Web/API/Response
                return response.blob();
            })
            .then((zip) => {
                // console.log(zip);

                // https://stuk.github.io/jszip/documentation/examples.html
                zip && JSZip.loadAsync(zip)
                    .then((files) => {
                        files.files[`${props.board.toUpperCase()}-${props.symbol.toUpperCase()}.json`].async("string")
                            .then((text) => {
                                let parsed = JSON.parse(text);
                                // console.log(parsed);

                                setData(parsed);
                                setError(null);
                            })
                            .catch((err) => {
                                console.log(
                                    `ERROR 1 Preview: ${props.board.toUpperCase()}-${props.symbol.toUpperCase()} ${err.message}`
                                );

                                setError(err.message);
                                setData(null);
                            })
                            .finally(() => {
                                setLoading(false);
                            });
                    })
                    .catch((err) => {
                        console.log(
                            `ERROR 2 Preview: ${props.board.toUpperCase()}-${props.symbol.toUpperCase()} ${err.message}`
                        );

                        setError(err.message);
                        setData(null);
                    })
            })
            .catch((err) => {
                console.log(
                    `ERROR 3 Preview: ${props.board.toUpperCase()}-${props.symbol.toUpperCase()} ${err.message}`
                );

                setError(err.message);
                setData(null);
            });

    }, []);

    // https://www.youtube.com/watch?v=1b1wC1ksJoI
    // https://bobbyhadz.com/blog/react-cannot-assign-to-current-because-read-only-property
    const chartRef = useRef<HTMLCanvasElement | null>(null)
    const chartInstance = useRef<Chart | null>(null)

    useEffect(() => {
        if (chartInstance.current) {
            chartInstance.current.destroy()
        }
        const myRef = chartRef.current?.getContext('2d')

        if (myRef && data) {
            let indices1 = data["1"].index;
            let array1 = data["1"].data[0]?.map((col, i) => data["1"].data.map(row => row[i]));

            let indices2 = data["2"].index;
            let array2 = data["2"].data[0]?.map((col, i) => data["2"].data.map(row => row[i]));

            const LAST_PRICE = array1 && array1[0].slice(-1)[0] || 1;
            const LAST_PREDICTED_DATE = indices2 && new Date(indices2.slice(-1)[0]) || new Date();

            let top_section = 0
            for (let i = 0; i < props.sections.length - 1; i++) {
                if (props.sections[i] > 0.9999) {
                    top_section = i;
                    break;
                }
            }
            
            const last_1 = array2 && array2[top_section].slice(-1)[0] || 1;
            const last_2 = array2 && array2[top_section + 1].slice(-1)[0] || 1;
            const LAST_PREDICTED_PRICE = (last_1 + last_2) / 2;

            let period = (Math.round(indices1.length / 300)) || 1;

            indices1 = indices1.filter((val, i) => i % period == 0);
            array1 = array1.map((row) => row.filter((val, i) => i % period == 0));

            // console.log("symbol:", props.symbol, "period:", period)

            // console.log(array1.length);

            let title = `${props.symbol.toUpperCase()} - ${props.description}`;
            title = `${title} (${(100 * (LAST_PREDICTED_PRICE - LAST_PRICE) / LAST_PRICE).toFixed(2)}%`;
            title = `${title}  ${LAST_PREDICTED_DATE.getUTCFullYear()}.${LAST_PREDICTED_DATE.getUTCMonth()})`;

            let lines = [
                {
                    yAxisID: "y1", data: array1[0], type: 'line',
                    pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'white',
                    borderColor: "#2a4858", borderWidth: 2, order: 1, fill: false
                },
                {
                    yAxisID: "y2", label: '', data: array1[1], type: 'bar',
                    // backgroundColor: "#d7301f",
                    // backgroundColor: "rgba(54, 162, 235, 0.5)",
                    backgroundColor: "rgba(215, 48, 31, 0.5)",
                    borderColor: 'white',
                    order: 2,
                },
                //////////////
            ];
            for (let i = 2; i < array1.length - 1; i++) {
                let alpha = props.sections[i-2];

                // console.log(maxima, interval, fraction, props.fractions[fraction]);
                
                // https://stackoverflow.com/questions/37866992/filling-area-between-two-lines-chart-js-v2
                (lines as any).push({
                    yAxisID: "y1", label: `-${i}`, data: array1[i], type: 'line',
                    pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: `rgba(0,0,255, ${alpha * 0.40})`,
                    borderColor: `rgba(0,0,255, ${alpha * 0.40})`, borderWidth: 0.1, order: 1, fill: '+1'
                })
            }
            (lines as any).push({
                yAxisID: "y1", label: `last`, data: array1.slice(-1)[0], type: 'line',
                pointRadius: 0.01, pointHoverRadius: 1, 
                backgroundColor: `rgba(0,0,255, ${props.sections.slice(-1)[0]})`,
                borderColor: `rgba(0,0,255, ${props.sections.slice(-1)[0]})`, 
                borderWidth: 0.1, order: 1, fill: false
            })

            chartInstance.current = new Chart(myRef, {
                type: 'bar',
                data: {
                    labels: indices1,
                    // https://www.color-hex.com/color-palette/31275
                    // https://www.color-hex.com/color-palette/27551
                    datasets: lines as any
                },
                options: {
                    responsive: true,
                    animation: {
                        duration: 0
                    },
                    plugins: {
                        title: {
                            display: true,
                            text: title
                        },
                        legend: {
                            display: false
                        },
                        // this one is taken from : chartjs-plugin-zoom/samples
                        zoom: {
                            pan: {
                                enabled: false,
                                mode: 'x'
                            },
                            // https://www.chartjs.org/chartjs-plugin-zoom/latest/guide/options.html
                            zoom: {
                                wheel: {
                                    enabled: false,
                                    speed: 0.3,
                                },
                                mode: 'x',
                                // Enable drag-to-zoom behavior
                                drag: {
                                    enabled: false
                                },
                            }
                        }
                    },
                    // https://www.chartjs.org/docs/latest/axes/
                    scales: {
                        x: {
                            type: 'time',
                        },
                        y1: {
                            type: 'linear',
                            position: 'left'
                        },
                        y2: {
                            type: 'linear',
                            position: 'right'
                        }
                    }
                },
            })
        }

        return () => {
            if (chartInstance.current) {
                chartInstance.current.destroy()
            }
        }
    }, [data]);

    return (
        <div>
            {loading &&
                <>
                    <Spinner animation="grow" variant="success" size="sm" />
                    ...
                </>
            }
            {
                data && <>
                    <div onClick={() => {
                        setShow(true);

                        // https://create-react-app.dev/docs/adding-custom-environment-variables/
                        (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${props.board}/${props.symbol}`)
                    }} >
                        <canvas ref={chartRef} />
                    </div>
                    <ModalMixed
                        symbol={props.symbol}
                        board={props.board}
                        data={data}
                        show={show}
                        fractions={[]}
                        sections={props.sections}
                        onHide={() => {
                            setShow(false);

                            // https://create-react-app.dev/docs/adding-custom-environment-variables/
                            (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${props.board}`);
                        }}
                    ></ModalMixed>
                </>
            }

        </div>
    );
}

export default Preview3;