import { Chart, TimeScale } from 'chart.js/auto';
import React, { useEffect, useRef, useState } from 'react';

import 'chartjs-adapter-luxon';
import zoomPlugin from 'chartjs-plugin-zoom';

import Spinner from 'react-bootstrap/Spinner';
import { Security } from './Preview';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import { Form } from 'react-bootstrap';

Chart.register(zoomPlugin, TimeScale);

interface Description {
    description: {
        columns: Array<string>,
        data: Array<[string, string, string, string, number, number, number | null]>
    }
}

interface IFaceLabels { 
    c:boolean, 
    i68: boolean, 
    i95: boolean, 
    i99: boolean,
    legend: boolean
}

// chart_create_mixed_bar_line_iss_securities_security_board_candles
// chartjs_create_mixed_bar_line
// map_key_security_description
// process_event_click_secid

function Mixed(props: { symbol: string, board: string, data: Security }) {
    const [labelInterval, setLabelInterval] = useState<number>(365);

    const [labels, setLabels] = useState<IFaceLabels>({
        c:true, i68: true, i95: true, i99: true, legend: false
    });

    // https://www.youtube.com/watch?v=1b1wC1ksJoI
    // https://bobbyhadz.com/blog/react-cannot-assign-to-current-because-read-only-property
    const chartRef = useRef<HTMLCanvasElement | null>(null)
    const chartInstance = useRef<Chart | null>(null)

    useEffect(() => {
        if (chartInstance.current) {
            chartInstance.current.destroy()
        }
        const myRef = chartRef.current?.getContext('2d')

        if (myRef && props.data) {
            let indices1 = props.data["1"].index;
            let array1 = props.data["1"].data[0]?.map((col, i) => props.data["1"].data.map(row => row[i]));

            let indices2 = props.data["2"].index;
            let array2 = props.data["2"].data[0]?.map((col, i) => props.data["2"].data.map(row => row[i]));

            const LAST_PRICE = array1 && array1[0].slice(-1)[0] || 1;

            // !!! https://stackoverflow.com/questions/38101859/chart-js-line-chart-with-different-labels-for-each-dataset
            //  index = [1, 2, 3, 4, 5]
            //   data = [9, 8, 7, 6, 5]
            // result = [{x: 1, y: 9}, {x: 2, y: 8}, ...]
            let mixIndexWithData = (index: number[], data: number[]) => {
                return index.map((index, i) => ({ x: index, y: data[i] }))
            }

            let scatter2 = [];
            if (array2) {
                for (let i = 0; i < array2.length; i++) {
                    scatter2[i] = mixIndexWithData(
                        indices2.filter((val, i) => i % labelInterval == 0),
                        array2[i].filter((val, i) => i % labelInterval == 0)
                    );
                    scatter2[i].shift();
                }
            }

            chartInstance.current = new Chart(myRef, {
                // https://chartjs-plugin-datalabels.netlify.app/guide/getting-started.html#registration
                plugins: [ChartDataLabels],
                type: 'bar',
                data: {
                    labels: indices1.concat(indices2),
                    // https://www.color-hex.com/color-palette/31275
                    // https://www.color-hex.com/color-palette/27551
                    datasets: [
                        {
                            yAxisID: "y1", data: mixIndexWithData(indices1, array1[0]), type: 'line',
                            pointRadius: 0.5, pointHoverRadius: 1, backgroundColor: 'white',
                            borderColor: "#2a4858", borderWidth: 2, order: 1, fill: false
                        },
                        {
                            yAxisID: "y1", label: '', data: [{ x: indices1.slice(-1)[0], y: LAST_PRICE }], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "#2a4858", borderWidth: 2, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: {
                                display: true,
                                align: 'start',
                                // https://chartjs-plugin-datalabels.netlify.app/guide/formatting.html#data-transformation
                                formatter: function (value, context) {
                                    let round = Math.round(value.y * 100) / 100;
                                    return `${round}`;
                                },
                                font: {
                                    weight: 'bold'
                                },
                                offset: 8
                            }
                        },
                        {
                            yAxisID: "y2", label: '', data: mixIndexWithData(indices1, array1[1]), type: 'bar',
                            // backgroundColor: "#d7301f",
                            // backgroundColor: "rgba(54, 162, 235, 0.5)",
                            backgroundColor: "rgba(215, 48, 31, 1)",
                            borderColor: 'white',
                            order: 2,
                        },
                        //////////////////// MAIN LINES
                        // https://stackoverflow.com/questions/37866992/filling-area-between-two-lines-chart-js-v2
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[8]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(255,0,0, 0.1)',
                            borderColor: "rgba(255,0,0, 0.1)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[6]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,255,0, 0.1)',
                            borderColor: "rgba(0,255,0, 0.1)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[4]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,0,255, 0.1)',
                            borderColor: "rgba(0,0,255, 0.1)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[2]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,0,255, 0.1)',
                            borderColor: "orange", borderWidth: 2, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[3]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,255,0, 0.1)',
                            borderColor: "rgba(0,0,255, 0.1)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[5]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(255,0,0, 0.1)',
                            borderColor: "rgba(0,255,0, 0.1)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices1, array1[7]), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'white',
                            borderColor: "rgba(255,0,0, 0.1)", borderWidth: 1.5, order: 1, fill: false
                        },
                        //////////////////// DASHED LINES (PREDICTED)
                        // https://stackoverflow.com/questions/44813714/dotted-line-in-chartjs
                        // https://www.chartjs.org/docs/latest/charts/line.html
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[6] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(255,0,0, 0.1)', borderDash: [10, 5],
                            borderColor: "rgba(255,0,0, 0.4)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[4] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,255,0, 0.1)', borderDash: [10, 5],
                            borderColor: "rgba(0,255,0, 0.4)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[2] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,0,255, 0.1)', borderDash: [10, 5],
                            borderColor: "rgba(0,0,255, 0.4)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[0] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,0,255, 0.1)', borderDash: [10, 5],
                            borderColor: "orange", borderWidth: 2, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[1] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(0,255,0, 0.1)', borderDash: [10, 5],
                            borderColor: "rgba(0,0,255, 0.4)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[3] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'rgba(255,0,0, 0.1)', borderDash: [10, 5],
                            borderColor: "rgba(0,255,0, 0.4)", borderWidth: 1.5, order: 1, fill: '+1'
                        },
                        {
                            yAxisID: "y1", label: '', data: mixIndexWithData(indices2, array2 && array2[5] || []), type: 'line',
                            pointRadius: 0.01, pointHoverRadius: 1, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(255,0,0, 0.4)", borderWidth: 1.5, order: 1, fill: false
                        },
                        ///////////////// SCATTER (DOTS)
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[0] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(74,29,90, 0.5)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.c }
                        },
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[1] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(69,52,126, 0.5)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.i68 }
                        },
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[2] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(69,52,126, 0.5)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.i68 }
                        },
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[3] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(72,99,144, 0.5)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.i95 }
                        },
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[4] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(72,99,144, 0.5)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.i95 }
                        },
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[5] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(135,180,167, 1)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.i99 }
                        },
                        {
                            yAxisID: "y1", label: '', data: scatter2.length && scatter2[6] || [], type: 'line',
                            pointRadius: 5.0, pointHoverRadius: 20.0, backgroundColor: 'white', borderDash: [10, 5],
                            borderColor: "rgba(135,180,167, 1)", borderWidth: 0.5, order: 1, fill: false,
                            // https://chartjs-plugin-datalabels.netlify.app/
                            datalabels: { display: labels.i99 }
                        },
                    ]
                },
                options: {
                    responsive: true,
                    animation: {
                        duration: 0
                    },
                    plugins: {
                        title: {
                            display: true,
                            text: props.symbol.toUpperCase()
                        },
                        legend: {
                            display: labels.legend
                        },
                        // this one is taken from : chartjs-plugin-zoom/samples
                        zoom: {
                            pan: {
                                enabled: true,
                                mode: 'x'
                            },
                            // https://www.chartjs.org/chartjs-plugin-zoom/latest/guide/options.html
                            zoom: {
                                wheel: {
                                    enabled: true,
                                    speed: 0.3,
                                },
                                mode: 'x',
                                // Enable drag-to-zoom behavior
                                drag: {
                                    enabled: false
                                },
                            }
                        },
                        // https://chartjs-plugin-datalabels.netlify.app/
                        datalabels: {
                            display: false,
                            // https://chartjs-plugin-datalabels.netlify.app/guide/formatting.html#data-transformation
                            formatter: function (value, context) {
                                // return context.chart.data.labels?[context.dataIndex];
                                // console.log(value);
                                // console.log(LAST);
                                let round = Math.round(value.y * 100) / 100;
                                let percent = Math.round(100 * (value.y - LAST_PRICE) / LAST_PRICE)
                                return `${round}\n(${percent}%)`;
                            },
                            color: 'black',
                            font: {
                                size: 10
                            },
                            align: 'right',
                            textAlign: 'center',
                        }
                    },
                    // https://www.chartjs.org/docs/latest/axes/
                    scales: {
                        x: {
                            type: 'time',
                        },
                        y1: {
                            type: 'linear',
                            position: 'left'
                        },
                        y2: {
                            type: 'linear',
                            position: 'right'
                        }
                    }
                },
            })
        }

        return () => {
            if (chartInstance.current) {
                chartInstance.current.destroy()
            }
        }
    }, [props.data, labels, labelInterval]);

    // This is for the metric counters
    const metr = (v: IFaceLabels) => {
        // If all labels enabled - do not make a separate counter hit
        if (Object.values(v).reduce((acc, curr) => acc & curr, true)) {
            return v;
        }

        const ed = (b: boolean) => b ? 'e' : 'd';
        let fragment = `l-${ed(v.c)}${ed(v.i68)}${ed(v.i95)}${ed(v.i99)}${ed(v.legend)}`;
        // https://create-react-app.dev/docs/adding-custom-environment-variables/
        (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${props.board}/${props.symbol}#${fragment}`);
        return v;
    }

    return (
        <div style={{alignContent: 'center', alignItems: 'center'}}>
            <canvas ref={chartRef} />
            <div>
                <Form.Check inline
                    label=""
                    type={'checkbox'}
                    checked={labels.legend}
                    onChange={() => {setLabels(metr({...labels, legend: labels.legend !== true }))}}
                />
                <Form.Check inline
                    label="  "
                    type={'checkbox'}
                    checked={labels.c}
                    onChange={() => {setLabels(metr({...labels, c: labels.c !== true }))}}
                />
                <Form.Check inline
                    label="  68%"
                    type={'checkbox'}
                    checked={labels.i68}
                    onChange={() => {setLabels(metr({...labels, i68: labels.i68 !== true }))}}
                />
                <Form.Check inline
                    label="  95%"
                    type={'checkbox'}
                    checked={labels.i95}
                    onChange={() => {setLabels(metr({...labels, i95: labels.i95 !== true }))}}
                />
                <Form.Check inline
                    label="  99%"
                    type={'checkbox'}
                    checked={labels.i99}
                    onChange={() => {setLabels(metr({...labels, i99: labels.i99 !== true }))}}
                />
            </div>
            <div>
                <b>  : </b>
                <Form.Check
                    inline
                    label="30 "
                    name="group1"
                    type={'radio'}
                    checked={labelInterval === 30}
                    onChange={() => { setLabelInterval(30) }}
                />
                <Form.Check
                    inline
                    label="60 "
                    name="group1"
                    type={'radio'}
                    checked={labelInterval === 60}
                    onChange={() => { setLabelInterval(60) }}
                />
                <Form.Check
                    inline
                    label="180 "
                    name="group1"
                    type={'radio'}
                    checked={labelInterval === 180}
                    onChange={() => { setLabelInterval(180) }}
                />
                <Form.Check
                    inline
                    label="365 "
                    name="group1"
                    type={'radio'}
                    checked={labelInterval === 365}
                    onChange={() => { setLabelInterval(365) }}
                />
            </div>
        </div>
    );
}

export default Mixed;