import { useEffect, useState } from 'react';
import './App.css';
import Preview from './Preview';

import 'bootstrap/dist/css/bootstrap.min.css';
import NavigationBar from './NavigationBar';
import BubbleBondYTM from './BubbleBondYTM';
import BubbleBondIssued from './BubbleBondIssued';
import { Form } from 'react-bootstrap';

import * as JSZip from 'jszip';
import { json } from 'stream/consumers';
import Preview2 from './Preview2';
import Preview3 from './Preview3';

export const BOARDS: { [key: string]: string } = {
    STOCKS: "Nasdaq: Stocks",
    ETFS: "Nasdaq: ETFs",
    TOPETFS: "Yahoo: TOP ETFs",
    BOND: "TQCB + TQOB: ",
    RFUD: "",
    ROPD: "",
    TQCB: "+:  - .",
    TQOB: "+:  - .",
    TQBR: "+:    - .",
    TQIF: "+:  - . ()",
    TQTF: "+: ETF - . ()",
    CETS: "  - . ()"
}

export interface Securities {
    data: Array<{
        s: string,
        ord1: number,
        ord2: number,
        last: number,
        i0: number,
        i1: number,
        i2: number,
        i3: number,
        i4: number,
        i5: number,
        i6: number,
        ss: Array<number>,
        NAME: string, //  
        ISSUEDATE: number, //   
        MATDATE: number, //  
        INITIALFACEVALUE: number, //   
        FACEUNIT: string, //  
        DAYSTOREDEMPTION: number, //   
        ISSUESIZE: number, //  
        FACEVALUE: number, //   
        ISQUALIFIEDINVESTORS: number, //    
        COUPONFREQUENCY: number, //     
        COUPONDATE: number, //   
        COUPONPERCENT: number, //  
        COUPONVALUE: number, //  ,   
        TYPENAME: string, // /  
        GROUP: string, //   
        TYPE: string, //  
        GROUPNAME: string, //  
        DURATION: number,
        YTM: number,

        fractions: Array<number>,
        sections: Array<number>,
    }>
}

function App() {
    // Common
    const [board, setBoard] = useState<string>('');
    const [secCount, setSecCount] = useState<number>(18);
    // Sections and Fractions Filters
    const [probability, setProbability] = useState<number>(0);
    // Interval Filters
    const [search, setSearch] = useState<string>('');
    const [interval, setInterval] = useState<number>(0);
    const [trend, setTrend] = useState<number>(0);
    // Bonds Filters
    const [excludedTypeNames, setExcludedTypeNames] = useState<Array<string>>([]);
    const [excludedFaceUnits, setExcludedFaceUnits] = useState<Array<string>>([]);
    // Fetch Json
    const [data, setData] = useState<Securities | null>(null);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);

    // TODO: THIS PART IS NOT FINISHED
    // EXAMPLE 1: https://nrsharip.github.io/securities/TQBR
    // Gives 404
    // EXAMPLE 2: https://nrsharip.github.io/securities
    // Gives window.location.pathname = '/securities'
    useEffect(() => {
        // https://stackoverflow.com/questions/39823681/read-the-current-full-url-with-react
        // console.log(window.location.pathname)
        // console.log(window.location.href)
        let board = window.location.pathname.repeat(1);
        board = board.replaceAll('/', '');
        board = board.replaceAll('securities', '');
        board = board || 'TQBR';

        setBoard(board);
    }, []);

    useEffect(() => {
        if (!board) { return; }

        // https://create-react-app.dev/docs/adding-custom-environment-variables/
        (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${board}`)

        let url = `output/A__${board}.json.zip`;

        fetch(url)
            .then((response) => {
                if (!response.ok) {
                    throw new Error(`This is an HTTP error: The status is ${response.status}`);
                }

                // https://developer.mozilla.org/en-US/docs/Web/API/Response
                return response.blob();
            })
            .then((zip) => {
                // console.log(zip);

                // https://stuk.github.io/jszip/documentation/examples.html
                zip && JSZip.loadAsync(zip)
                    .then((files) => {

                        files.files[`A__${board}.json`].async("string")
                            .then((text) => {
                                let parsed = JSON.parse(text);
                                console.log(parsed);

                                setData(parsed);
                                setError(null);
                            })
                            .catch((err) => {
                                console.log(
                                    `ERROR 1 App: ${board.toUpperCase()}} ${err.message}`
                                );

                                setError(err.message);
                                setData(null);
                            })
                            .finally(() => {
                                setLoading(false);
                            });
                    })
                    .catch((err) => {
                        console.log(
                            `ERROR 2 App: ${board.toUpperCase()}} ${err.message}`
                        );

                        setError(err.message);
                        setData(null);
                    })
            })
            .catch((err) => {
                console.log(
                    `ERROR 3 App: ${board.toUpperCase()}} ${err.message}`
                );

                setError(err.message);
                setData(null);
            });
    }, [board]);

    // https://blog.logrocket.com/3-ways-implement-infinite-scroll-react/
    const handleScroll = () => {
        if (data && secCount > data.data.length) {
            return
        }

        if (window.innerHeight + document.documentElement.scrollTop <= document.documentElement.offsetHeight * 0.95) {
            // console.log(window.innerHeight, document.documentElement.scrollTop, document.documentElement.offsetHeight);
            return;
        }
        console.log("Scroll: ", secCount);

        setSecCount(secCount + 6);
    };

    // https://stackoverflow.com/questions/60540985/react-usestate-doesnt-update-in-window-events
    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, [secCount]);

    let bondsFiltered = data && data.data
        .filter((v) => {
            if (!v.sections || !v.sections.length || !probability) return true;

            let section = 0
            for (let i = 0; i < v.sections.length - 1; i++) {
                if (v.last >= v.ss[i] && v.last < v.ss[i + 1]) {
                    section = i;
                    break;
                }
            }
            // console.log(probability, v.s, section, v.sections[section], v.sections);
            return (probability === 1) ? v.sections[section] >= 0 && v.sections[section] < 0.2 :
                (probability === 2) ? v.sections[section] >= 0.2 && v.sections[section] < 0.4 :
                    (probability === 3) ? v.sections[section] >= 0.4 && v.sections[section] < 0.6 :
                        (probability === 4) ? v.sections[section] >= 0.6 && v.sections[section] < 0.8 :
                            (probability === 5) ? v.sections[section] >= 0.8 && v.sections[section] <= 1.0 :
                                true;
        })
        .filter((v) => (
            (interval === 0) ? true :
                (interval === 1) ? v.last >= v.i0 && v.last < v.i1 :
                    (interval === 2) ? v.last >= v.i1 && v.last < v.i2 :
                        (interval === 3) ? v.last >= v.i2 && v.last < v.i3 :
                            (interval === 4) ? v.last >= v.i3 && v.last < v.i4 :
                                (interval === 5) ? v.last >= v.i4 && v.last < v.i5 :
                                    (interval === 6) ? v.last >= v.i5 && v.last <= v.i6 :
                                        true
        ))
        // .filter((v) => (
        //     (trend === 0) ? true :
        //         (trend === 1) ? v.ord2 >= 0 :
        //             (trend === 2) ? v.ord2 < 0 :
        //                 true
        // ))
        // Bonds
        .filter((v) => (
            (v.TYPENAME === undefined) ? true :
                (!excludedTypeNames.includes(v.TYPENAME)) ? true : false
        ))
        .filter((v) => (
            (v.FACEUNIT === undefined) ? true :
                (!excludedFaceUnits.includes(v.FACEUNIT)) ? true : false
        ))
        .sort((a, b) => {
            if (trend === 1) {
                return b.ord2 - a.ord2
            } else if (trend === 2) {
                return a.ord2 - b.ord2
            } else {
                return b.ord1 - a.ord1
            }
        });

    let filtered = bondsFiltered && bondsFiltered
        .filter((v) => (
            (search === '') ? true :
                v.s.toLowerCase().includes(search.toLowerCase()) ||
                v.NAME.toLowerCase().includes(search.toLowerCase())
        ))
        // .sort((a, b) => (b.ord1 - a.ord1));

    let typeNames = new Set<string>();
    data && data.data.forEach((v) => { typeNames.add(v.TYPENAME); })

    let faceUnits = new Set<string>();
    data && data.data.forEach((v) => { faceUnits.add(v.FACEUNIT); })

    // console.log("typeNames", typeNames);
    // console.log("faceUnits", faceUnits);

    return (
        <>
            <NavigationBar
                maximum={filtered ? filtered.length : 0}
                current={filtered ? filtered.slice(0, secCount).length : 0}
                interval={interval}
                probability={probability}
                trend={trend}
                board={board}
                search={search}
                sections={filtered && filtered.length > 0 ? filtered[0].sections : []}
                fractions={filtered && filtered.length > 0 ? filtered[0].fractions : []}
                onBoardChange={(board) => {
                    setBoard(board);
                    setInterval(0);
                    setProbability(0);
                    setTrend(0);
                    setSearch('');
                    setSecCount(18);
                }}
                onIntervalPick={(n: number) => {
                    let value = interval === n ? 0 : n;
                    // https://create-react-app.dev/docs/adding-custom-environment-variables/
                    value && (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${board}#i-${value}`)
                    setInterval(value);
                    setSecCount(18);
                }}
                onProbabilityPick={(n: number) => {
                    let value = probability === n ? 0 : n;
                    // https://create-react-app.dev/docs/adding-custom-environment-variables/
                    value && (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${board}#p-${value}`)
                    setProbability(value);
                    setSecCount(18);
                }}
                onTrendPick={(n: number) => {
                    let value = trend === n ? 0 : n
                    // https://create-react-app.dev/docs/adding-custom-environment-variables/
                    value && (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${board}#t-${value}`)
                    setTrend(value);
                    setSecCount(18);
                }}
                onSearchChange={(s: string) => {
                    // https://create-react-app.dev/docs/adding-custom-environment-variables/
                    s && (window as any).ym(process.env.REACT_APP_YM_COUNTER, "hit", `${board}#s-${s}`)
                    setSearch(s);
                    setSecCount(18);
                }}
            />
            <div style={{ width: '100%', padding: "0 3% 0 3%" }}>
                {
                    bondsFiltered && (board === 'BOND') && (
                        <div
                            style={{ width: '66%', float: 'left' }}
                        >
                            <div
                                style={{ width: '30%', float: 'left' }}
                            >
                                <div

                                >
                                    <b>/  :</b>
                                    {
                                        Array.from(typeNames).map((typeName) =>
                                            <Form.Check
                                                key={typeName}
                                                type='checkbox'
                                                label={typeName}
                                                checked={!excludedTypeNames.includes(typeName)}
                                                onChange={(value) => {
                                                    // console.log(value.target.checked);

                                                    // https://react.dev/learn/updating-arrays-in-state
                                                    if (excludedTypeNames.includes(typeName)) {
                                                        setExcludedTypeNames(excludedTypeNames.filter(
                                                            (name) => name !== typeName
                                                        ))
                                                    } else {
                                                        setExcludedTypeNames([...excludedTypeNames, typeName])
                                                    }
                                                }}
                                            />
                                        )
                                    }
                                </div>
                                <div

                                >
                                    <b> :</b>
                                    {
                                        Array.from(faceUnits).map((faceUnit) =>
                                            <Form.Check
                                                key={faceUnit}
                                                type='checkbox'
                                                label={faceUnit}
                                                checked={!excludedFaceUnits.includes(faceUnit)}
                                                onChange={(value) => {
                                                    // console.log(value.target.checked); 

                                                    // https://react.dev/learn/updating-arrays-in-state
                                                    if (excludedFaceUnits.includes(faceUnit)) {
                                                        setExcludedFaceUnits(excludedFaceUnits.filter(
                                                            (name) => name !== faceUnit
                                                        ))
                                                    } else {
                                                        setExcludedFaceUnits([...excludedFaceUnits, faceUnit])
                                                    }
                                                }}
                                            />
                                        )
                                    }
                                </div>
                            </div>

                            <div
                                style={{ width: '70%', float: 'left' }}
                            >
                                <div

                                >
                                    <BubbleBondIssued
                                        secs={{ data: bondsFiltered }}
                                        onSecurityPick={(sec) => setSearch(sec)}
                                        search={search}
                                    />
                                </div>
                                <div

                                >

                                    <BubbleBondYTM
                                        secs={{ data: bondsFiltered }}
                                        onSecurityPick={(sec) => setSearch(sec)}
                                        search={search}
                                    />
                                </div>
                            </div>
                        </div>
                    )
                }
                {
                    // https://stackoverflow.com/questions/40497505/displaying-divs-in-rows
                    filtered && filtered
                        .slice(0, secCount)
                        .map((sec) =>
                            <div
                                key={sec.s}
                                className="security"
                                style={{ width: '33%', float: 'left' }}
                            >
                                {
                                    (!sec.fractions || sec.fractions.length == 0) &&
                                    (!sec.sections || sec.sections.length == 0) &&
                                    <Preview
                                        key={sec.s}
                                        symbol={sec.s}
                                        board={board}
                                        description={sec.NAME}
                                    />
                                }
                                {
                                    sec.fractions && sec.fractions.length > 0 &&
                                    <Preview2
                                        key={sec.s}
                                        symbol={sec.s}
                                        board={board}
                                        description={sec.NAME}
                                        fractions={sec.fractions}
                                    />
                                }
                                {
                                    sec.sections && sec.sections.length > 0 &&
                                    <Preview3
                                        key={sec.s}
                                        symbol={sec.s}
                                        board={board}
                                        description={sec.NAME}
                                        sections={sec.sections}
                                    />
                                }
                            </div>
                        )
                }
            </div>
        </>
    );
}

export default App;
