import { Chart, TimeScale } from 'chart.js/auto';
import React, { useEffect, useRef, useState } from 'react';

import 'chartjs-adapter-luxon';
import zoomPlugin from 'chartjs-plugin-zoom';

import Spinner from 'react-bootstrap/Spinner';
import { Security } from './Preview';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import { Form } from 'react-bootstrap';
import { Bubble, getElementAtEvent } from 'react-chartjs-2';
import { Securities } from './App';

Chart.register(zoomPlugin, TimeScale);

// chart_create_mixed_bar_line_iss_securities_security_board_candles
// chartjs_create_mixed_bar_line
// map_key_security_description
// process_event_click_secid

// https://react-chartjs-2.js.org/examples/bubble-chart/
function BubbleBondIssued(props: {
    secs: Securities,
    onSecurityPick: (sec: string) => void,
    search: string
}) {
    const chartRef = useRef();

    // https://react-chartjs-2.js.org/docs/working-with-events/
    const onClick = (event: React.MouseEvent<HTMLCanvasElement, MouseEvent>) => {
        if (!chartRef.current) {
            return;
        }

        let element = getElementAtEvent(chartRef.current, event);

        if (!element || !element[0]) {
            // disable deselect on empty space -
            // it screws up the pan deselecting each time
            // props.onSecurityPick('');
            return;
        }

        console.log(props.secs.data[element[0].index]);

        let symbol = (props.search === props.secs.data[element[0].index].s) ? '' :
            props.secs.data[element[0].index].s

        props.onSecurityPick(symbol);
    }

    let values = props.secs.data.map((sec) => ({
        x: sec.ISSUEDATE,
        y: sec.COUPONPERCENT,
        r: Math.exp(sec.ord1 / 800),
    }))

    let issueDates = props.secs.data.map((sec) => sec.ISSUEDATE);
    // console.log("min", Math.min(...issueDates));
    // console.log("max", Math.max(...issueDates));

    let durations = props.secs.data.map((sec) => sec.DURATION);
    let durationMin = Math.log(Math.min(...durations));
    let durationMax = Math.log(Math.max(...durations));
    let durationSpread = durationMax - durationMin;

    let bgcolors = props.secs.data.map((sec) => {
        let r = 255 * (Math.log(sec.DURATION) - durationMin) / durationSpread;
        let b = 255 * (1 - (Math.log(sec.DURATION) - durationMin) / durationSpread);

        if (props.search === sec.s) {
            return `rgba(255, 0, 0, 1.0)`;
        }

        return `rgba(${r}, 0, ${b}, 0.3)`;
    })

    let brwidths = props.secs.data.map((sec) => {
        if (props.search === sec.s) {
            return 5;
        }

        return 2 * (Math.log(sec.DURATION) - durationMin) / durationSpread;
    })

    let labels = props.secs.data.map((sec) => sec.s);

    return (
        <Bubble
            ref={chartRef}
            data={{
                labels,
                datasets: [
                    {
                        // label: '',
                        data: values,
                        borderWidth: brwidths,
                        // borderColor: 'black',
                        backgroundColor: bgcolors,
                    },
                ],
            }}
            options={{
                responsive: true,
                animation: {
                    duration: 0
                },
                plugins: {
                    title: {
                        display: true,
                        text: 'x -  , y -   (%),  - ,  - '
                    },
                    legend: {
                        display: false
                    },
                    zoom: {
                        pan: {
                            enabled: true,
                            mode: 'x'
                        },
                        // https://www.chartjs.org/chartjs-plugin-zoom/latest/guide/options.html
                        zoom: {
                            wheel: {
                                enabled: true,
                                speed: 0.3,
                            },
                            mode: 'x',
                            // Enable drag-to-zoom behavior
                            drag: {
                                enabled: false
                            },
                        }
                    },
                },
                scales: {
                    x: {
                        // https://www.chartjs.org/docs/latest/axes/cartesian/linear.html
                        type: 'time',
                        min: Math.min(...issueDates),
                        max: Math.max(...issueDates),
                    },
                    y: {
                        beginAtZero: false,
                    }
                },
            }}

            onClick={onClick}
        />
    );
}

export default BubbleBondIssued;