import { Chart, TimeScale } from 'chart.js/auto';
import React, { useEffect, useRef, useState } from 'react';

import 'chartjs-adapter-luxon';
import zoomPlugin from 'chartjs-plugin-zoom';

import Spinner from 'react-bootstrap/Spinner';
import { Security } from './Preview';

import Modal from 'react-bootstrap/Modal';
import Mixed from './Mixed';
import Mixed2 from './Mixed2';
import Mixed3 from './Mixed3';

Chart.register(zoomPlugin, TimeScale);

interface Description {
    description: {
        columns: Array<string>,
        data: Array<[string, string, string, string, number, number, number | null]>
    }
}

// chart_create_mixed_bar_line_iss_securities_security_board_candles
// chartjs_create_mixed_bar_line
// map_key_security_description
// process_event_click_secid

function ModalMixed(props: {
    show: boolean,
    onHide: () => void,
    symbol: string,
    board: string,
    data: Security,
    fractions: Array<number>,
    sections: Array<number>
}) {
    const [description, setDescription] = useState<Description | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    useEffect(() => {
        fetch(`https://iss.moex.com/iss/securities/${props.symbol.toUpperCase()}.json`)
            .then((response) => {
                if (!response.ok) {
                    throw new Error(`This is an HTTP error: The status is ${response.status}`);
                }
                return response.json();
            })
            .then((json) => {
                // console.log(json);

                setDescription(json);
                setError(null);
            })
            .catch((err) => {
                console.log(err.message);

                setError(err.message);
                setDescription(null);
            })
            .finally(() => {
                setLoading(false);
            });
    }, []);

    let modalSymbol = props.symbol.toUpperCase();
    let modalDescription = "";

    if (description && description.description.data.length > 0) {
        modalDescription = description.description.data[1][2]
    }

    return (
        <Modal show={props.show} fullscreen={true} onHide={props.onHide}>
            <Modal.Header closeButton>
                <Modal.Title>
                    {modalSymbol} <a onClick={() => {
                        // https://www.freeiconspng.com/images/question-icon
                        // https://stackoverflow.com/questions/4907843/open-a-url-in-a-new-tab-and-not-a-new-window
                        window.open(`https://www.google.com/search?q=${modalSymbol}`)?.focus()
                    }}><img src='question-icon-5.jpg' width={25}></img></a> - {modalDescription} {
                        // https://www.freeiconspng.com/images/question-icon
                        modalDescription && <a onClick={() => {
                            // https://stackoverflow.com/questions/4907843/open-a-url-in-a-new-tab-and-not-a-new-window
                            window.open(`https://www.google.com/search?q=${modalDescription} wiki`)?.focus()
                        }}><img src='question-icon-5.jpg' width={25}></img></a>
                    }
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div style={{ height: '100%' }}>
                    {
                        props.data &&
                        <div style={{ minHeight: '100%', minWidth: '60%', float: 'left' }}>
                            {
                                props.fractions.length == 0 &&
                                props.sections.length == 0 &&
                                <Mixed symbol={props.symbol} board={props.board} data={props.data} />
                            }
                            {
                                props.fractions.length > 0 &&
                                <Mixed2 symbol={props.symbol} board={props.board} data={props.data} fractions={props.fractions} />
                            }
                            {
                                props.sections.length > 0 &&
                                <Mixed3 symbol={props.symbol} board={props.board} data={props.data} sections={props.sections} />
                            }
                        </div>
                    }
                    <div style={{ minHeight: '100%', minWidth: '35%', maxWidth: '35%', float: 'left', marginLeft: '10px' }}>
                        {
                            loading &&
                            <>
                                <Spinner animation="grow" variant="success" />
                                ...
                            </>
                        }
                        {
                            description &&
                            <table style={{ fontSize: 12 }}>
                                <tbody>
                                    {description.description.data.map((val, i) => {
                                        return <tr key={i}>
                                            <td style={{ width: '50%' }} align='right'><b>{val[1]}:</b></td>
                                            <td
                                                style={{ width: '50%' }}
                                                // https://stackoverflow.com/questions/19266197/reactjs-convert-html-string-to-jsx
                                                dangerouslySetInnerHTML={{ __html: val[2] }}
                                            />
                                        </tr>
                                    })}
                                </tbody>
                            </table>
                        }
                    </div>
                </div>
            </Modal.Body>
        </Modal>
    );
}

export default ModalMixed;